.react-calculator {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  max-width: 100%;
  overflow: hidden;
  margin: 5px;
  border-radius: 5px;
  border: solid 2px rgb(45, 45, 45);
  padding: 0 0.9rem;
  background-color: #fff;
}

.react-calculator .result-panel {
  display: flex;
  padding-top: 1rem;
  flex: 2 1;
  flex-direction: column;
  text-align: right;
  padding: 2px 1rem;
  line-height: 3rem;
  overflow: visible;
}

.react-calculator .result-panel .last-row {
  flex: 1 1;
  color: #969ba3;
  font-size: 25px;
  align-items: flex-end;
  display: flex;
  justify-content: flex-end;
  overflow: auto;
}

.react-calculator .result-panel .cur-row {
  flex: 1 1;
  color: #46494d;
  font-size: 30px;
  overflow: hidden;
  max-width: 100%;
  padding-bottom: 3px
}

.react-calculator .button-panel {
  flex: 5 1;
}

.react-calculator .button-panel .button {
  cursor: pointer;
  position: relative;
  margin: 0;
  padding: 0;
  box-shadow: inset 1px 1px 0 0 #e3e7e9;
  border: none;
  background-color: #fafafa;
  font-size: 30px;
  line-height: 0px;
  text-align: center;
  color: #979ca4;
  overflow: hidden;
}

.react-calculator .button-panel .button:before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #aaa;
  opacity: 0;
}

.react-calculator .button-panel .button.clicked:before {
  -webkit-animation: react-calculator-click 0.5s ease-out 0s 1 alternate forwards;
          animation: react-calculator-click 0.5s ease-out 0s 1 alternate forwards;
}

.react-calculator .button-panel .button.button-equal {
  color: #fff;
  background-color: #fa722e;
}

.react-calculator .button-panel .button:focus {
  outline: none;
}

@-webkit-keyframes react-calculator-click {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0.7;
  }
  100% {
    -webkit-transform: scale(3);
            transform: scale(3);
    opacity: 0;
  }
}

@keyframes react-calculator-click {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0.7;
  }
  100% {
    -webkit-transform: scale(3);
            transform: scale(3);
    opacity: 0;
  }
}

